import { useEffect, useRef, useState } from "react";
import ForceGraph3D from "react-force-graph-3d";
import { NavLink } from "react-router-dom";
import Cookies from "universal-cookie";

import { DiscourseDisplay } from "../../components/compounds/discoursedisplay/discoursedisplay";
import { ringNameDisplay } from "../../components/elements/ringnamedisplay";

import { linarrow } from "../../assets";

import "./GraphView.css";

const reorder = (displayValues) => {
	let discourse = [...displayValues];
	let start = discourse.splice(discourse.findIndex((i) => i === "S\u2081"));
	return start.concat(discourse);
};

const GraphView = ({ graphData }) => {
	let graphRef = useRef();
	useEffect(() => {
		const graphElement = graphRef.current;
		graphElement.d3Force("link").distance((link) => link.length);
	}, []);
	const cookies = new Cookies();

	let namesCookie = cookies.get("discourseNames");
	let discourseCookie = cookies.get("namedDiscourses");
	let ringCookie = cookies.get("ringNames");
	let sequenceCookie = cookies.get("ringSequences");

	const operationNames = [
		"Agent \u2194 Truth",
		"Other \u2194 Agent",
		"Product \u2194 Other",
		"Truth \u2194 Product",
	];
	const operations = ["swAT", "swOA", "swPO", "swTP"];

	let tempNodeColor = null;
	let tempNodeId = null;
	let tempLinkIndex = null;
	let tempLinkColor = null;

	const [infoData, setInfoData] = useState([" ", " ", " ", " "]);
	const [nodeHoverState, setNodeHoverState] = useState(false);
	const [nodeClicked, setNodeClicked] = useState();
	const [linkClicked, setLinkClicked] = useState();

	const handleNodeHover = (discourse) => {
		switch (discourse) {
			case "clear":
				setNodeHoverState(false);
				break;
			default:
				setInfoData(JSON.parse(discourse));
				setNodeHoverState(true);
				break;
		}
	};

	const handleNodeClick = (node) => {
		switch (node) {
			case nodeClicked:
				setNodeClicked();
				break;
			default:
				setNodeClicked(node);
				tempNodeColor = node.color;
				tempNodeId = node.id;
				break;
		}
	};

	const handleLinkClick = (link) => {
		switch (link) {
			case linkClicked:
				setLinkClicked();
				break;
			default:
				setLinkClicked(link);
				tempLinkColor = link.color;
				tempLinkIndex = link.index;
				console.log({ tempLinkIndex });
				break;
		}
	};

	return (
		<div className="graph-view-container">
			<NavLink className="link home-link" to="/">
				Home
			</NavLink>
			{nodeHoverState ? (
				<div className="discourse-info">
					<DiscourseDisplay
						namedDiscourses={discourseCookie}
						discourseNames={namesCookie}
						displayValues={infoData}
						quickAdd={false}
						ringNameDisplay={ringNameDisplay(
							ringCookie,
							sequenceCookie,
							infoData,
							reorder
						)}
					/>
				</div>
			) : null}

			{nodeClicked ? (
				<div className="discourse-info-focused">
					<DiscourseDisplay
						namedDiscourses={discourseCookie}
						discourseNames={namesCookie}
						displayValues={JSON.parse(nodeClicked.id)}
						quickAdd={false}
						ringNameDisplay={ringNameDisplay(
							ringCookie,
							sequenceCookie,
							infoData,
							reorder
						)}
					/>
				</div>
			) : null}
			{linkClicked ? (
				<div className="link-info-focused">
					<div className="link-info-source">
						<DiscourseDisplay
							namedDiscourses={discourseCookie}
							discourseNames={namesCookie}
							displayValues={JSON.parse(linkClicked.source.id)}
							quickAdd={false}
							ringNameDisplay={ringNameDisplay(
								ringCookie,
								sequenceCookie,
								infoData,
								reorder
							)}
						/>
					</div>
					<div className="link-info-operation">
						<div className="link-info-name">{operationNames[operations.indexOf(linkClicked.name)] || "Rotation"}</div>
						<img src={linarrow} className="link-info-arrow" />
					</div>
					<div className="link-info-target">
						<DiscourseDisplay
							namedDiscourses={discourseCookie}
							discourseNames={namesCookie}
							displayValues={JSON.parse(linkClicked.target.id)}
							quickAdd={false}
							ringNameDisplay={ringNameDisplay(
								ringCookie,
								sequenceCookie,
								infoData,
								reorder
							)}
						/>
					</div>
				</div>
			) : null}

			<ForceGraph3D
				graphData={graphData}
				backgroundColor={"#aaaaaa"}
				nodeOpacity={0.8}
				linkOpacity={0.9}
				linkWidth={(link) => (link.name === "rotation" ? 1.2 : 0.6)}
				linkDirectionalArrowLength={(link) =>
					link.name === "rotation" ? 8 : 5
				}
				linkDirectionalArrowRelPos={1}
				ref={graphRef}
				onNodeHover={(node) =>
					node ? handleNodeHover(node.id) : handleNodeHover("clear")
				}
				onNodeClick={(node) => {
					handleNodeClick(node);
				}}
				onLinkClick={(link) => {
					handleLinkClick(link);
				}}
				nodeColor={(node) =>
					node === nodeClicked
						? "gold"
						: node.id === tempNodeId
						? tempNodeColor
						: node.color
				}
				linkColor={(link) =>
					link === linkClicked
						? "gold"
						: link.index === tempLinkIndex
						? tempLinkColor
						: link.color
				}
			/>
		</div>
	);
};

export default GraphView;
